@use "base/variables" as v;
@use "base/mixins" as m;

.contenido-contacto {
	@include m.tablet {
		@include m.grid(2, v.$separacion);
		background-image: url(../img/granos_cafe.jpg);
		background-repeat: no-repeat;
		background-position: right center;
		background-size: 40%;
	}
}

.formulario {
	.campo {
		margin-bottom: 2rem;

		&:last-of-type {
			margin-bottom: 0;
		}

		label {
			display: block;
			text-transform: uppercase;
			font-size: 2.2rem;
			font-weight: 700;
		}

		input:not([type="submit"]),
		select {
			width: 100%;
			padding: 1rem;
			border: 0.5rem solid v.$primario;
		}

		input[type="submit"] {
			background-color: v.$primario;
			width: 100%;
			color: v.$blanco;
			border: none;
			padding: 1rem;
			font-size: 2.2rem;
			font-weight: 700;
			text-transform: uppercase;
			transition: background-color 300ms ease-in-out;

			&:hover {
				cursor: pointer;
				background-color: v.$secundario;
			}
		}
	}
}
